@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;700&display=swap');

.main-content {
  max-width: 700px;
  margin: 0 auto;
  padding: 2rem;
  font-family: 'Noto Sans KR', sans-serif;
  line-height: 1.6;
  color: #333;
}

.blog-intro {
  text-align: center;
  margin-bottom: 3rem;
}

.blog-intro h1 {
  font-size: 2.5rem;
  font-weight: 700;  /* 블로그 제목 굵기를 700으로 변경 */
  margin-bottom: 0.5rem;
}

.blog-intro p {
  font-size: 1.2rem;
  color: #666;
}

.blog-posts {
  margin-bottom: 3rem;
}

.blog-post {
  margin-bottom: 3rem;
  border-bottom: 1px solid #eee;
  padding-bottom: 2rem;
}

.blog-post:last-child {
  border-bottom: none;
}

.blog-post h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.post-date {
  font-size: 0.9rem;
  color: #888;
  margin-bottom: 1rem;
}

.post-excerpt {
  margin-bottom: 1rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.read-more {
  display: inline-block;
  color: #007bff;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.read-more::after {
  content: ' >';  /* "더 읽기"를 "더 읽기 >"로 수정 */
}

.read-more:hover {
  color: #0056b3;
}

.about-blog {
  background-color: #f8f9fa;
  padding: 2rem;
  border-radius: 5px;
}

.about-blog h2 {
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .main-content {
    padding: 1rem;
  }

  .blog-intro h1 {
    font-size: 2rem;
  }

  .blog-post h2 {
    font-size: 1.5rem;
  }
}